import React from 'react';
// eslint-disable-next-line
import { reject } from 'q';
import {Link} from 'react-router-dom'

import axios from 'axios'



export default class UserSignUp extends React.Component{

     
    state={
      form_submission : {status :'Fill the Field Below to Sign Up',apply_style:'w3-small w3-center login_req_status w3-black'},
      form_status :'Sign up',
      label_username : {label_Content : "Your FullName",label_style:"w3-text-black"},
      label_email : {label_Content : "Your Email Address",label_style:"w3-text-black"},
      label_password : {label_Content : "Desired Password",label_style:"w3-text-black"},
      label_cpassword : {label_Content : "Confirm Password",label_style:"w3-text-black"},
      inputs_disable : 'false',
      formInput : {username:'',email:'',password:'',cpassword:''}
    }
  
   
  render(){
              

    var formSubmission = (e)=>{
        e.preventDefault();  
        
       
        var upStatuus = new Promise((resolve,reject)=>{
          this.setState({
            ...this.state,
            label_username : {label_Content : "Your FullName",label_style:"w3-text-black w3-small"},
            label_email : {label_Content : "Your Email Address",label_style:"w3-text-black w3-small"},
            label_password : {label_Content : "Desired Password",label_style:"w3-text-black w3-small"},
            label_cpassword : {label_Content : "Confirm Password",label_style:"w3-text-black w3-small"},
            form_submission : {status :'Processing your request ...',apply_style:'w3-small w3-center login_req_status w3-black w3-text-white'},
            form_status :'Submitting...'
           })

            
          resolve();
        })

        var sendData = new Promise((resolve,reject)=>{
          
            axios.post('/signUpUser',this.state.formInput).then(res=> {return res.data}).then(data=>{
               
               return resolve(data)
             })
            
        })

        var handleErrorsRecieved = (errors_recieved)=>{
          
                               
                     if(errors_recieved.res_status==="validation_error"){
                    
                        this.setState({
                          ...this.state,
                          form_submission : {status :'Correct the Highlighted Errors below.',apply_style:'w3-small w3-center login_req_status w3-black w3-text-red'},
                          form_status :'Sign Up'
                        })
                        errors_recieved.res_content.forEach(errorv=>{
                            
                          
                          this.setState({
                            ...this.state,
                            ['label_'+errorv.param]:{label_Content : errorv.msg,label_style:"w3-text-red w3-small"}
                          })
                        })
                        
         

                     }else if(errors_recieved.res_status==="server_error"){
                     
                        this.setState({
                          ...this.state,
                          form_submission : {status :'We cannot Complete Your Request at the Moment.Try again Later.',apply_style:'w3-small w3-center login_req_status w3-black w3-text-red'},
                          form_status :'Sign Up'
                        })
                      
                     }else if(errors_recieved.res_status==="email_registered"){
                          this.setState({
                            ...this.state,
                            form_submission : {status :'The Email adress you provided is already Registered.',apply_style:'w3-small w3-center login_req_status w3-black w3-text-red'},
                            form_status :'Sign Up'
                          })
                       
                     }else if(errors_recieved.res_status==="success"){
                      this.setState({
                        ...this.state,
                        form_submission : {status :'Registration Was Succesfull.Check your Email inbox to Verify your Account',apply_style:'w3-small w3-center login_req_status w3-black w3-text-green'},
                        form_status :'Sign Up',
                        formInput : {username:'',email:'',password:'',cpassword:''}
                      })
                     }
               
        }

        upStatuus.then(()=>{return sendData}).then((serverResponse)=>{
             handleErrorsRecieved(serverResponse)
          })
          
    }
    
    var handleFormInput=(e)=>{
      this.setState({
        ...this.state,
        formInput: {...this.state.formInput,[e.target.id]:e.target.value}

      })
    }
    
    return(
      
      <div id="login-page w3-container">
        <div id="login-form" className="w3-card-4 w3-hover-shadow">
        <div className="w3-green w3-text-black w3-center w3-center w3-xlarge">Create an Account</div>
        <p className={this.state.form_submission.apply_style} >{this.state.form_submission.status}</p>
         <form  onSubmit={formSubmission} className="w3-margin-bottom" >
             <label htmlFor="username"  id="l_username" className={this.state.label_username.label_style}>{this.state.label_username.label_Content}</label>
             <input onChange={handleFormInput}  value={this.state.formInput.username} className="w3-input w3-center" name="username" type="text" id="username" placeholder="FullName" />
             <label htmlFor="email"  className={this.state.label_email.label_style} id="l_email">{this.state.label_email.label_Content}</label>
             <input onChange={handleFormInput} value={this.state.formInput.email} className="w3-input w3-center" name="email" type="email" id="email" placeholder="email" />
             <label htmlFor="password" className={this.state.label_password.label_style}  id="l_password">{this.state.label_password.label_Content}</label>
             <input onChange={handleFormInput} value={this.state.formInput.password} className="w3-input w3-center" name="password" type="password" id="password"  placeholder="Password" />
             <label  htmlFor="cpassword" className={this.state.label_cpassword.label_style}  id="l_cpassword">{this.state.label_cpassword.label_Content}</label>
             <input onChange={handleFormInput} value={this.state.formInput.cpassword} className="w3-input w3-center" type="password" name="cpassword" id="cpassword"  placeholder="Confirm password" />
             <button   className="w3-button w3-black w3-margin-bottom w3-round-xlarge w3-hover-black w3-hover-text-blue" id="loginbtn" type="submit">{this.state.form_status}</button>
         </form>
          <Link to="/login" className="w3-padding " style={{"color":"purple","textDecoration":"underline","fontSize":"1em","marginTop":"100px"}}>Login to your account</Link>
        </div>
      </div>

     )
  }


}



