import React from 'react';
import avatar from '../profile.png'
import axios from 'axios'
import {Link} from 'react-router-dom'

class Homepage extends React.Component{
    state={
        doctorsProfiles :[]
    }
  
    
   
    

    componentDidMount(){
        
        
        axios.get('/doctors').then(res=>{
             this.setState({
                 ...this.state,
                  doctorsProfiles:res.data
                  
               })
              
            
        })
    
     }
    render(){
       
       var {nextRoute} =this.props
             
        var docdata=()=>{
            if(this.state.doctorsProfiles.length >0 ){
                return(
                    this.state.doctorsProfiles.map(doctor=>{
                        var docId = '/setAppointment?docId='+doctor.doc_id
                        
                         return( 
                         <div onClick={()=>{nextRoute(docId)}} key={doctor.doc_id} className="ind-doc w3-col m3 w3-hover-shadow  w3-hover-white w3-margin-bottom">
                         <Link  to={docId}>
                                 <img alt="doctor_avatar" src={avatar} ></img> 
                                 <div className="doctor-desc">
                                         <div>{doctor.name}</div> 
                                         <div>Gender : {doctor.gender}</div> 
                                         <div>HealthCenter : {doctor.healthcare}</div>  
                                         <div>Speciaist in : {doctor.specialities} </div>
                                 </div>
                         </Link>
                         </div>                   
                         )
                    })
                )
                
            }else if(this.state.doctorsProfiles.length ===0){
             return(
                <div className="w3-panel w3-pale-yellow w3-large w3-center " >Loading Doctors' Profiles <span className="w3-animate-fade">....</span> </div>     
             )
            }else{
                return(
                    <div className="w3-panel w3-pale-blue w3-large w3-center" >Oops,No doctors to show</div>     
                )
            }
        }       
           
       
       
        return(
           <div id="homepage-section"  className="`content-section ">
           <div className="w3-panel w3-purple w3-center w3-text-black">Select a Doctor Below to setup a medical appointment with them</div>
              <div   id="doctorprofile" className="w3-row">
              {docdata()}
              </div>
            </div>
           
        )


    }

}

export default Homepage;