import React from 'react';
 // eslint-disable-next-line
import { reject } from 'q';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom'


import axios from 'axios';



 class LoginUser extends React.Component{
 
  state={
    loggedIn_user:{userEmail:'',userName:''},
    loginStatus :{message:"Fill the Fields below to Login",styling:"w3-center w3-black w3-text-white w3-small"},
    userCredentials :{username:'',password:''},
    username_label:{content:"Username",styling:"w3-small w3-text-back"},
    password_label:{content:"Password",styling:"w3-small w3-text-back"}
  
}
     
         

   handleLogin =(e)=>{
    e.preventDefault();

    new Promise((resolve,reject)=>{
      this.setState({
        ...this.state,
        username_label:{content:"Username",styling:"w3-small w3-text-back"},
        password_label:{content:"Password",styling:"w3-small w3-text-back"},
        loginStatus :{message:"Processing ...",styling:"w3-center w3-black w3-text-white w3-small"},
      
      })
      resolve()
    }).then(()=>{
      if(this.state.userCredentials.username.length<4 || this.state.userCredentials.username===null || this.state.userCredentials.username===''){
        this.setState({
          ...this.state,
          username_label:{content:"Username is Required",styling:"w3-small w3-text-red"},
          loginStatus :{message:"Correct the Highlighted Errors Below",styling:"w3-center w3-black w3-text-red w3-small"}
        })
       }else if(this.state.userCredentials.password.length<4 || this.state.userCredentials.password===null || this.state.userCredentials.password===''){
           this.setState({
          ...this.state,
            password_label:{content:"Passwod is Required",styling:"w3-small w3-text-red"},
            loginStatus :{message:"Correct the Highlighted Errors Below",styling:"w3-center w3-black w3-text-red w3-small"}
          })
       }else{
           axios.post('/login',this.state.userCredentials).then((res)=>{
         

             if(res.data.loginMsg==="Success" && res.status===200){
              
              this.setState({
                ...this.state,
                username_label:{content:"Username",styling:"w3-small w3-text-back"},
                password_label:{content:"Password",styling:"w3-small w3-text-back"},
                loginStatus :{message:"Login Successfull",styling:"w3-center w3-black w3-text-green w3-small"},
                userCredentials :{username:'',password:''}
              
              })
               if(this.props.nextRoute===null){
                 
                  this.props.history.push('/myaccount')
                  this.props.loginUser()
                
                }else{
                  this.props.loginUser()
                  this.props.history.push(this.props.nextRoute)
                  
                }
                
              
              
            
            
             }else if(res.data.loginMsg==="Invalid_login_credentials" && res.status===200){
              this.setState({
                ...this.state,
                username_label:{content:"Username",styling:"w3-small w3-text-back"},
                password_label:{content:"Password",styling:"w3-small w3-text-back"},
                loginStatus :{message:"Invalid Username and Password Combinations",styling:"w3-center w3-black w3-text-red w3-small"}
                
              
              })
             }
           }).catch(err=>{
            this.setState({
              ...this.state,
              username_label:{content:"Username",styling:"w3-small w3-text-back"},
              password_label:{content:"Password",styling:"w3-small w3-text-back"},
              loginStatus :{message:"We could not complete your request at the moment.Try again later",styling:"w3-center w3-black w3-text-red w3-small"}
              
            
            })
          })
        
      
       }
    }).catch(err=>console.log(err))
    
   }
  
   handleFormInput=(e)=>{
     this.setState({
       ...this.state,
      userCredentials : {...this.state.userCredentials,[e.target.name]:e.target.value}
     })
   }
   
  render(){
 
    return(
      <div id="login-page w3-container">
        <div id="login-form" className="w3-card-4 w3-hover-shadow">
        <div className="w3-green w3-text-black w3-center w3-center w3-xlarge">Login</div>
        <p className={this.state.loginStatus.styling}>{this.state.loginStatus.message}</p>
         <form onSubmit={this.handleLogin} >
             <label htmlFor="username" className={this.state.username_label.styling}  id="l_username">{this.state.username_label.content}</label>
             <input onChange={this.handleFormInput} name="username" value={this.state.userCredentials.username} className="w3-input w3-center" type="email" id="username" placeholder="Email" />
             <label htmlFor="password" className={this.state.password_label.styling}  id="l_password">{this.state.password_label.content}</label>
             <input onChange={this.handleFormInput} name="password" value={this.state.userCredentials.password} className="w3-input w3-center" type="password" id="password"  placeholder="Password" />
             <button  className="w3-button w3-black w3-margin-bottom w3-round-xlarge w3-hover-black w3-hover-text-blue" id="loginbtn" type="submit">Login</button>
         </form>

         <Link to='/signup' className="w3-text-blue">Create an Account</Link>
        </div>
       
      </div>

     )

  

}
 
}

var mapActiontoProps =(dispatch)=>{
   return{
     loginUser : ()=>{dispatch({type:"set-authentication",status:true})}
   }
}
export default connect(null,mapActiontoProps)(LoginUser);
