import React from 'react'
import axios from 'axios'
import {connect} from 'react-redux'


class  Myaccount extends React.Component{
    state={
       
        userData:null
    }

    redirectUser=()=>{
        this.props.history.push('/login')
    }
    componentDidMount(){
        
         axios.get('/user/dashboard').then((res)=>{
          
             if(res.data.data!==null && res.data.message==="success"){
                 this.setState({
                     userData : res.data.data
                 })
             }else{
                this.redirectUser()
             }
         }).catch((err)=>{
             console.log(err)
         })
    }
        NoloadedData =()=>{
            if(this.state.userData===null){
            return (
                <div>
                    <div className="w3-panel w3-pale-yellow">Loading your Appointments <span className="w3-animate-fading">....</span> </div>
                </div>
            )}else if(this.state.userData!==null && this.state.userData.length<1){
              return(
             <div>
                <div className="w3-panel w3-pale-yellow">You have no appointments Currently</div>
            </div>
              )  
            }

        }
        loadedData=()=>{
            
            if(this.state.userData!==null){
                return(
                    this.state.userData.map(dataRecieved=>{
                        
                        return(
                            
                                <tr key={dataRecieved._id}>
                                <td>{dataRecieved.appointment_date}</td> 
                                <td>{dataRecieved.docname}</td>
                                <td>{dataRecieved.subject}</td>
                                <td>{dataRecieved.add_info}</td>
                                </tr>  
                        )
                    })
                )
                
         } 
        }
    render(){
                        
              
              
        return(
            <div className="w3-center w3-margin-top">
            
             <table className="w3-table-all">
             <thead>
             <tr>
               <th>Appointment Schedule</th>
               <th>Doctor Booked</th>
               <th>Appointment Subject</th>
               <th>Additional Information</th>
             </tr>
             </thead>
             <tbody>
             {this.loadedData()} 
             </tbody>
        
             </table>
           {this.NoloadedData()}
           
            </div>
     
     
         )
    }


 
} 

var mapStatetoProps=(state)=>{
    return{
        logged_status:state.loggedIn
    }
}
export default connect(mapStatetoProps) (Myaccount);