import React from 'react';
import {BrowserRouter,Route,Switch} from 'react-router-dom'
import Homepage from './components/homepage'
import './w3.css'
import './index.css'
import LoginUser from './components/login'
 import UserSignUp from './components/signup'
 import SetAppointment from './components/setappointment'
import Navbar from './components/navbar'
import axios from 'axios';
import About from './components/about'
import Myaccount from './components/myaccount'
import{connect} from 'react-redux'

//import axios from 'axios'





 class App extends React.Component{
      state={
         
          profile_details :{email:null,displayName:null},
          next_route:null
          }


        
          componentDidMount(){
            fetch('/userStatus').then(res=>{return res.json()}).then((data)=>{
             
                if(data.authentication===true && data.user!==undefined){
                   
                   this.setState({
                       ...this.state,
                       user_loggenIn:data.authentication,
                       profile_details:{email:data.user.patientEmail,displayName:data.user.patientName}
                   })
                   this.props.updateStore(true)
                   
                }else if(data.authentication===false && data.user===undefined){
                   this.setState({
                    ...this.state,
                    user_loggenIn :false,
                    profile_details :{email:null,displayName:null}
                   })
                   this.props.updateStore(false)
                  
                }
      
               })
          }
          
      
 
   

    AuthenticatedLogin =(user)=>{
         this.setState({
            ...this.state,
            user_loggenIn :true,
            profile_details :{email:user.patientEmail,displayName:user.patientName}
         })
    }

    setNextRoute=(route)=>{
        
            this.setState({
                ...this.state,
                next_route:route
            })
                
    }

    handleRoutes =()=>{
          if(this.props.user_loggenIn === false){
             return(
             <React.Fragment>
                  <Switch>
                   <Route exact path='/'   render={props=>    {return <Homepage nextRoute={this.setNextRoute} {...props} /> }} />
                    <Route path='/setAppointment' render={props=>{ return <LoginUser nextRoute={this.state.next_route}  {...props} AuthenticatedLogin={this.AuthenticatedLogin} /> }}/>
                    <Route path='/signup' render={props=>{return <UserSignUp nextRoute={this.state.next_route} {...props} /> } } />
                    <Route path='/login' render={props=>{ return <LoginUser nextRoute={this.state.next_route} {...props}  />   } } />
                    <Route path='/logout' render={props=>    {return <Homepage nextRoute={this.setNextRoute} {...props} /> }}/>
                    <Route path='/about' component={About}/>
                    <Route path='/myaccount' render={props=>{ return(<LoginUser  {...props} AuthenticatedLogin={this.AuthenticatedLogin} />)   } }></Route>
                    <Route  path='/*' render={props=>    {return <Homepage nextRoute={this.setNextRoute} {...props} /> }}/>

                    </Switch>
             </React.Fragment>
             )
          }else if(this.props.user_loggenIn === true){
            return(
                <React.Fragment>
                    <Switch>
                    <Route exact path='/'   render={props=>    {return <Homepage nextRoute={this.setNextRoute} {...props} /> }} />
                    <Route path='/setAppointment' render={ props=>{ return(<SetAppointment {...props} userEmail={this.state.profile_details.email} nextRoute={this.state.next_route} />)  }} />
                    <Route path='/logout' render={props=>    {return <Homepage  nextRoute={this.setNextRoute} {...props} /> }}/>
                    <Route path='/myaccount' render={props=>    {return <Myaccount  nextRoute={this.setNextRoute} {...props} />}}/>
                    <Route path='/about' render={props=>    {return <About  nextRoute={this.setNextRoute} {...props} />}}/>
                    <Route  path='/*' render={props=>    {return <Homepage  nextRoute={this.setNextRoute} {...props} /> }}/>
                 </Switch>
                </React.Fragment>
            )
          }
      }

     logoutUser=()=>{
         axios.get('/logout').then((res)=>{
           if(res.data==="User_logged_out"){
            this.setState({
                ...this.state,
                profile_details :{email:null,displayName:null},
                next_route:null
             })
            
             this.props.logUserOut();
             
           }
            
             
         }).catch(err=>console.log(err))
     } 

    render(){
        
               
        return(
            <BrowserRouter>
                <div id="page-wrapper">
                 <div id="content-wrap">
                 <Navbar  loggeduser={this.state.profile_details}   logoutUser={this.logoutUser} Userauthentication = {this.state.user_loggenIn}/>   
                   {this.handleRoutes()}
                    
                    {/* <Route path='/setAppointment?docId=' component={SetAppointment}/>*/}
                  
                 
                 </div>
                  

                <div id="footer-section"><p style={{"textAlign":"center"}}>Copyright  2019</p></div>            
                </div> 
            </BrowserRouter>
           
        )
    }

    
}

var mapStatetoProps = (state)=>{
    return{
        user_loggenIn:state.loggedIn
    }
}

var mapDispatchtoProps =(dispatch)=>{
    return{
        updateStore : (status)=>{ dispatch({type:'set-authentication',status:status}) },
        logUserOut :()=>{dispatch({type:"log_user_out",status:false})}
    }
}


export default connect(mapStatetoProps,mapDispatchtoProps)(App);