import React from 'react'
import {Link} from 'react-router-dom'
import {connect} from 'react-redux'


 function Navbar(props){


  var {logoutUser} = props;
  

   var manageRoutes = function(){
    if(props.user_is_loggedIN===true){
 
        return(
           <div>
                      <Link className="navItem " to='/'>Home</Link>
                       <Link className="navItem" to='/myaccount'>My Account</Link>
                       <Link className="navItem " to='/about'>About</Link>
                       <Link className="navItem w3-hover-text-red" onClick={logoutUser} to='/'>Logout</Link>
                    
                        
           </div>
        )
   }else if(props.user_is_loggedIN===false){
       return(
           <div>
                    <Link className="navItem w3-hover-text-white" to='/'>Home</Link>
                    <Link className="navItem w3-hover-text-white" to='/login'>Login</Link>
                    <Link className="navItem w3-hover-text-white" to='/signup'>Sign Up</Link>
                    <Link className="navItem w3-hover-text-white" to='/about'>About</Link>
                    
           </div>
       )
      
   }
   } 
    return(
        <div className=" w3-center" id="navbar-section">

          <div className="nav-items"  >
           
             {manageRoutes()}

        </div>  
        </div>
 
 
     )

  
} 

var mapStatetoProps =(state)=>{
    return{
        user_is_loggedIN: state.loggedIn
    }
}
export default connect(mapStatetoProps)(Navbar);