import React from 'react';
import {Link}  from 'react-router-dom';
import axios from 'axios';




export default class SetAppointment extends React.Component{
  
 
        state={
          booking_status:"Submit",
          setAnotherAppoin:'false',
          formData :{desired_date:'',appointment_subject:'',a_information:'',doc_to_book:''},
          doc_name :'',
          l_appointment_reason:{styling:"w3-tiny w3-text-black",content:"Describe Your Appointment Subject"},
          l_desiredDate :{label_style:"w3-tiny ",content:'Pick A Desired Appointment Date '},
          mandatory_att: '*',
          doc_avaiability:'not-checked',
          form_submission_status : {content:'Step 1 : Check Doctor Availability.',style:"login_req_status w3-black w3-center w3-small"}
        }
  componentDidMount(){
    

    fetch('/doctor'+this.props.location.search).then(data=>{return data.json()}).then((doctoInfo)=>{
             doctoInfo.length===0 ?(
        this.setState({
          ...this.state,
         doc_name: 'No_Doctor_Found'
        })
         
      ):(
        doctoInfo.forEach(doctor => {
          this.setState({
            ...this.state,
           doc_name: doctor.name
          })
     
        })
      )
      
     
      
    })
      
  }
  anotherAppointment=()=>{
    this.setState({
      ...this.state,
      booking_status:"Submit",
      setAnotherAppoin:'false',
      formData :{desired_date:'',appointment_subject:'',a_information:'',doc_to_book:''},
      l_desiredDate :{label_style:"w3-tiny ",content:'Pick A Desired Appointment Date '},
      mandatory_att: '*',
      doc_avaiability:'not-checked',
      form_submission_status : {content:'Step 1 : Check Doctor Availability.',style:"login_req_status w3-black w3-center w3-small w3-text-white"}
    })
  }
  handleFormSubmission=(e)=>{
    e.preventDefault();
  
    if(e.target[1].value.length<1  || e.target[1].value==='' || e.target[0].value===undefined ){
      this.setState({
        ...this.state,
        l_appointment_reason:{styling:"w3-tiny w3-text-red ",content:"Describe Your Appointment Subject"},
      })
    }else{
      this.setState({
        ...this.state,
      l_appointment_reason:{styling:"w3-tiny w3-text-black",content:"Describe Your Appointment Subject"},   
     l_desiredDate :{label_style:"w3-tiny ",content:'Pick A Desired Appointment Date '},
      booking_status:"Submitting ...",
      form_submission_status : {content:'Step 2 : Processing Your Appointment Request.',style:"login_req_status w3-black w3-text-white w3-center w3-small"}
        
     })
     fetch('/userStatus').then(res=>{return res.json()}).then((data)=>{
       
      if(data.authentication===true && data.user!==undefined){
        this.setState({
          ...this.state,
          formData :{...this.state.formData,bookedBy:data.user.patientEmail}
        })
        var formDta = this.state.formData
        
    
          axios.post('/setAppointment',formDta).then(response=>{
            console.log( response.data  )   
            if(response.status===200 && response.data==="Success"){
              this.setState({
                ...this.state,
                setAnotherAppoin:'true',
                booking_status:"Submit",
                doc_avaiability:'not-checked',
                l_desiredDate :{label_style:"w3-tiny ",content:'Appointment Succesfully Scheduled for :'},
                formData :{...this.state.formData,appointment_subject:'',a_information:'',doc_to_book:''},
              form_submission_status : {content:'Success. Your appointment with '+this.state.doc_name + ' has been scheduled for '+ this.state.formData.desired_date +' Succesfully',style:"login_req_status w3-black  w3-center w3-tiny"} 
              })
            
            }else if(response.status===200 && response.data==="Empty_Appointment_Subject"){
              this.setState({
                ...this.state,
                l_appointment_reason:{styling:"w3-tiny w3-text-red ",content:"Describe Your Appointment Subject"},
                booking_status:"Submit",
                doc_avaiability:'not-checked',
                l_desiredDate :{label_style:"w3-tiny ",content:'Appointment Succesfully Scheduled for :'},
                formData :{...this.state.formData,appointment_subject:'',a_information:'',doc_to_book:''},
              form_submission_status : {content:'Correct the Errors Highlighted in Red.',style:"login_req_status w3-black  w3-center w3-tiny"} 
            })

            }else if(response.status===200 && response.data==="Doctor_not_Found"){
              this.setState({
                ...this.state,
                booking_status:"Submit",
                doc_avaiability:'not-checked',
                l_desiredDate :{label_style:"w3-tiny ",content:'Pick A Desired Appointment Date '},
                formData :{desired_date:'',appointment_subject:'',a_information:'',doc_to_book:''},
              form_submission_status : {content:'The doctor with an ID of '+(this.props.location.search).slice(7,) + ' could not be found on our database.Try Reloading the Page',style:"login_req_status w3-text-red w3-black  w3-center w3-small"} 
            })
            }else if(response.data==="Choose a Future Date"){
        
              this.setState({
                ...this.state,
               booking_status:"Submit",
                doc_avaiability:'not-checked',
                l_desiredDate :{label_style:"w3-tiny w3-text-red",content:'Choose a date in the future.'},
                formData :{...this.state.formData,appointment_subject:'',a_information:'',doc_to_book:''},
              form_submission_status : {content:'Correct the Errors Highlighted in Red.',style:"login_req_status w3-black w3-text-red w3-center w3-tiny"} 
            })
            }else{
              this.setState({
                ...this.state,
                booking_status:"Submit",
                doc_avaiability:'not-checked',
                l_desiredDate :{label_style:"w3-tiny ",content:'Pick A Desired Appointment Date '},
                formData :{desired_date:'',appointment_subject:'',a_information:'',doc_to_book:''},
              form_submission_status : {content:'We could not Complete your Request at the Moment.Try Again Later',style:"login_req_status w3-text-red w3-black  w3-center w3-small"} 
              })
            }
          
          })
         
      }else if(data.authentication===false && data.user===undefined){
        this.setState({
          ...this.state,
        l_appointment_reason:{styling:"w3-tiny w3-text-black",content:"Describe Your Appointment Subject"},   
       l_desiredDate :{label_style:"w3-tiny ",content:'Pick A Desired Appointment Date '},
        booking_status:"Submitting ...",
        form_submission_status : {content:'Error :You Need To be Logged In to Proceed.',style:"login_req_status w3-black w3-text-red  w3-center w3-small"}
          
       })
       
      }

     })
     
    }
    

  }
  
  checkAvailability =(e)=>{
   
    this.setState({
      ...this.state,
      l_desiredDate :{label_style:"w3-text-purple w3-tiny",content:'Checking Availability Status...'},
      mandatory_att: '',
      doc_avaiability:'not-checked',
      form_submission_status : {content:'Step 1 : Check Doctor Availability.',style:"login_req_status w3-black w3-text-white w3-center w3-small"}
       

    })
    var docid=(this.props.location.search).slice(7,);
    var date_selected =e.target.value
   
    fetch('/doctorAvailability?date_picked='+e.target.value+'&docId='+ docid).then((response)=>{
      return response.json();
    }).then((recdata)=>{
     
          if(recdata==='Doc_Available'){
            this.setState({
              ...this.state,
              l_desiredDate :{label_style:"w3-tiny w3-text-green",content:'Doctor Available on the Selected Date .'},
              formData :{...this.state.formData,desired_date:date_selected,doc_to_book:(this.props.location.search).slice(7,)},
              doc_avaiability:'available',
              form_submission_status : {content:'Step 2 : Describe your Appointment Subject.',style:"login_req_status w3-text-white w3-black w3-center w3-small"}
          
            })
          }else if(recdata==="Doctor_not_Found"){
            this.setState({
              ...this.state,
              l_desiredDate :{label_style:"w3-tiny w3-text-red",content:'Doctor with an ID of '+ (this.props.location.search).slice(7,) + " cannot be Found."},
              doc_avaiability:'Doc_Not_Available'
            })
          }else{

          
            this.setState({
              ...this.state,
              l_desiredDate :{label_style:"w3-tiny w3-text-red",content:'Doctor Not Available on '+ date_selected },
                 doc_avaiability:'Doc_Not_Available'
            })
          }
    }).catch(err=>console.log(err))


    
    

  }
  handleInput=(e)=>{
    this.setState({
         ...this.state,
         formData :{...this.state.formData,[e.target.id]:e.target.value}
       
    })
   }
    
   varInitializeForm =()=>{
    if(this.state.doc_avaiability==='available'){
      return(
      <div>
        <label htmlFor="appointment_subject"  id="a_subject" className={this.state.l_appointment_reason.styling}> {this.state.l_appointment_reason.content}  <span className="w3-text-red">*</span></label>
        <input value={this.state.formData.appointment_subject} onChange={this.handleInput}  className="w3-input w3-center w3-small" type="text-Area" id="appointment_subject" name="appointment_subject" placeholder="Describe your Reasons for requesting an appointment" />
        <label htmlFor="a_information"  id="a_info" className="w3-tiny">Additional Information</label>
         <input value={this.state.formData.a_information} onChange={this.handleInput} className="w3-small w3-input w3-center" type="text" id="a_information" name="add_info"  placeholder="Additional Information" />      
        <button className=" w3-button w3-black w3-margin-bottom w3-round-xlarge w3-hover-black w3-hover-text-blue" id="loginbtn" type="submit">{this.state.booking_status}</button>
      </div>
      )
    }if(this.state.doc_avaiability==='Doc_Not_Available'){
        return(null)
    }else if(this.state.setAnotherAppoin==='true'){
        return(
           <button onClick={this.anotherAppointment} className="w3-center w3-button w3-pale-green w3-margin-bottom w3-margin-top  w3-hover-green w3-hover-text-black" style={{"width":"60%","marginLeft":"20%"}}>Set Another Appointment</button>
          )
    }else{
      return(null)
    }
   }

   check_date_input=()=>{
    if(this.state.setAnotherAppoin!=='true'){
      return(<div>
         <label htmlFor="pickDate"  id="l_pickDate" className={this.state.l_desiredDate.label_style}> {this.state.l_desiredDate.content}<span className="w3-small w3-text-red">{this.state.mandatory_att}</span></label>
         <input value={this.state.formData.desired_date} onChange={this.checkAvailability} className="w3-input w3-small w3-center" name="desired_app_date" type="date" id="pickDate" placeholder="Choose Desired Date" />
                 
      </div>)
    }else{
      return(null)
    }
   }
 render(){
   
   var check_whether_toDisplayForm =()=>{
    if(this.state.doc_name ==='' ){
      return(
      <div className="w3-panel w3-pale-yellow w3-text-black w3-center">Fetching Doctor's Profile. Please Wait ...</div>
      )
    }else if(this.state.doc_name ==='No_Doctor_Found'){
     
       return (
       <div className="w3-panel w3-pale-red w3-text-black w3-center w3-large w3-border w3-border-black">Oops.We couldn't find the Doctor with an id of {(this.props.location.search).slice(7,)}. <br/><br/><Link className="w3-small w3-button w3-black w3-text-white w3-hover-black w3-hover-shadow w3-round-xxlarge" to='/'>Click here to Find Available Doctors</Link> </div>
       )
   }else{
      return(
          <div id="login-form" className="w3-card-4 w3-hover-shadow">
            <div className="w3-blue w3-text-black w3-center w3-center ">Complete the form below to Book an Appointment with <br/> <span style={{"fontWeight":"bold"}}>{this.state.doc_name}</span></div>
            <p className={this.state.form_submission_status.style} >{this.state.form_submission_status.content}</p>
            <form  className="w3-margin-bottom" onSubmit={this.handleFormSubmission}>
               {this.check_date_input()}
               {this.varInitializeForm()}
             

            </form>
          
          </div>
      )
   }
  
 }
 return(
  <div id="login-page w3-container">
    {check_whether_toDisplayForm()}
  </div>

 )

}

}



