var initialState={
    loggedIn:null
};


 var authentication_status = (state=initialState,action)=>{
    if(action.type==='set-authentication' && action.status===true) {
            state ={
                ...state,
                loggedIn:true
            }
            
   
     }else if(action.type==='set-authentication' && action.status===false){
        state ={
            ...state,
            loggedIn:false
        }
        
     }else if(action.type==='log_user_out' && action.status===false){
        
         state={
            ...state,
            loggedIn:false
         }
       
     }   
     return state; 
    
    
 }


 export {authentication_status};